/* ParticleView */

.particle_container {
  background: midnightblue;
  padding: 0;
  margin: 0;
  height: 100vh;
}

.particle_container_title {
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

/* Navigation */

.active_nav {
  border-bottom: 1px solid darkgoldenrod;
}

/* Summary */

#Summary {
  background: #ffffff;
  overflow: hidden;
  max-width: 100vw;
}

.image_container {
  overflow: hidden;
  min-height: 100vh;
}

#summary_profile_picture {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

#summary_text {
  min-height: 80vh;
}

/* Experience */

#experience {
  min-height: 100vh;
  /* background: #e6e8ea; */
  background: whitesmoke;
}

.profile_image_container {
  /* background: lightblue; */
  height: 30vh;
  width: 30vh;
  border-radius: 15vh;
  left: 12vh;
  position: relative;
  overflow: hidden;
}

.profile_image {
  height: 31vh;
  width: 31vh;
}

.profile_text_container {
  background: white;
  min-height: 30vh;
  width: 50vw;
  overflow: hidden;
  padding: 12px 12px 0px 14vh;
}

/* Portfolio */

#portfolio {
  width: 100vw;
  min-height: 100vh;
  background: white;
}

.project_card {
  text-align: center;
  height: 350px;
  width: 350px;
  position: relative;
  background: white;
}
.project_image {
  position: absolute;
  height: 350px;
  width: 350px;
  z-index: 2;
  top: 0px;
  left: 0px;
  background: white;
}

.animation_top {
  top: 225px;
  position: relative;
  background: lightcoral;
}

.animation_image {
  background: red;
}

.animation_bottom {
  /* bottom: 25px; */
  /* left: 135px; */
  /* position: absolute; */
}

/* Code Snippets */

#CodeSnippet {
  min-height: 100vh;
  max-height: 200vh;
}

#imageRow {
  background: #282922;
  color: yellowgreen;
}

#titleContainer {
  max-width: 500px;
}

#stepsContainer {
  max-height: 48vh;
  overflow: scroll;
  padding: 20px 0px 0px 20px;
}

#solutionContainer {
  /* background: red; */
  max-width: 500px;
}

.titleContainer {
  max-width: 200px;
  /* background: red; */
}

#tempimage {
  height: 100vh;
  background: lightseagreen;
}

/* USED ABOVE */

#image_container {
  min-height: 50vh;
  max-height: 65vh;
  width: 50vw;
  background: lightblue;
}

#below_image_container {
  min-height: 35vh;
  max-height: 50vh;
  width: 50vw;
  background: lightyellow;
}

#info_container {
  height: 50vh;
  width: 50vw;
  background: lightgreen;
}

#steps_container {
  height: 50vh;
  width: 50vw;
  overflow: scroll;
  background: lightpink;
}

/* Footer */

#Footer {
  padding: 15vh 0vh 0vh 0vh;
  margin: 0%;
  min-height: 100vh;
  background: midnightblue;
  text-align: center;
  color: white;
  position: relative;
}

.contact_container {
  min-height: 350px;
}

.subfooter_references {
  color: black;
}

#subfooter {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-size: 12px;
  background: #cccccc;
  padding: 15px 0px 15px 0px;
  color: black;
  margin: 0px;
}

/* Themes */

.primary_text_color {
  color: #fefefe;
}

.secondary_text_color {
  color: #f7cf56;
}

.tertiary_text_color {
  color: #44507f;
}

.primary_color {
  background: midnightblue;
}

/* Utilities */

.separator {
  height: 33vh;
  width: 100vw;
  position: relative;
}

.separator_heading {
  position: absolute;
  left: 12vw;
  bottom: 6vh;
}

