/* Text and Font */

.primary_heading_color {
  color: midnightblue;
}

input[type="text"] {
  color: #44507f;
  background: #e8e9ef;
}

input[type="text"]:focus {
  color: darkgoldenrod;
  background: #e8e9ef;
}

textarea[type="text"] {
  color: #44507f;
  background: #e8e9ef;
}

textarea[type="text"]:focus {
  color: darkgoldenrod;
  background: #e8e9ef;
}

