/* 
Modal stuff below found for the Recipe Sections
*/

.modalx {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

#modal-main {
  position: fixed;
  color: #44507f;
  background: #ffffff;
  /* min-width: 640px; */
  max-width: 28vw;
  height: 89vh;
  top: 9%;
  left: 30%;
  overflow: scroll;
  padding: 1%;
  text-align: left;
}

.modal_image {
  width: 25vw;
  height: 25vw;
  padding: 0px 0px 10px 0px;
}

.modal_hr {
  padding: 0px;
  margin: 0px 0px 8px 0px;
}

hr {
  background: #fefefe;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

